// Main Theme Button Style
body {
    .btn {
        background-color: $blue;
        color: $purple;
        padding: 12px 30px;
        @include primary-font;
        font-weight: 700;
        cursor: pointer;
        transition: .3s ease all;
        border-radius: 30px;
        font-size: 16px;
        border: 0px;

        &:hover {
            background-color: $purple;
            color: $blue;
            transition: .3s;
        }
    }
}



.simple-link {
    color: $blue;
    font-weight: 700;
    text-decoration: none;
    font-size: 18px;
    @include ease;

    svg {
        width: 22px;
        margin-left: 6px;
        @include ease;

        path {
            @include ease;
        }
    }

    &:hover {

        color: $purple;
        @include ease;

        svg {
            transform: translateX(10px);
            @include ease;

            path {
                fill: $blue;
                @include ease;
            }

        }
    }
}


// Extra Button Styles - Can be used for a simple link that is not formatted as buttons